import { AjaxGet } from './request.js'

//获取选车页面数据
function getSelectCarInfo() {
    let url = "http://yapi.demo.qunar.com/mock/23134/carList"
    return new Promise(function (resolve) {
        resolve(AjaxGet(url))
    })
}

